import React from 'react';

export function ContactForm() {
    return (<div className='contactForm'>
            <h1>Mensaje</h1>
            <input className='textInput' type="text" required autoComplete='name' placeholder='Nombre'/>
            <input className='textInput' type="tel" required autoComplete='tel' placeholder='Telefono'/>
            <input className='textInput' type="email" autoComplete='email' placeholder='Correo electronico'/>
            <input className='textInput' type="text" placeholder='Mensaje'/>
            <input className='buttonInput' type="submit" value='Enviar mensaje'/>
        </div>
    );
}

