import './Css/ProjectsPage.scss';
import { Project } from '../Components/Project';
import nasaPhoto from '../Assets/Img/nasa.jpeg';


export function ProjectsPage() {
	return <div className='projectsPage'>
		
		
		<Project
			title='Photo of the day NASA app'
			imagen={ nasaPhoto }
			category='Website'
			tech1='React js'
			tech2='Nasa API'
			tech3='Webstorm IDE'
			description='Aplicación de uso didáctico para el aprendizaje en temas astronómicos,
			 basada el la API de la NASA, que muestra desde la base de datos distintas fotografías
			 y su descripción basadas en sus diferentes fecha'
		/>
		
		<Project
			title='App de coctelería'
			category='Android app'
			tech1='Flutter'
			tech2='Firebase'
			tech3='Android studio'
			description='Aplicación y software para gestión de recetas de bebidas, desarrollado para bares y restaurantes
			con desarrollo de API y gestión de base de datos en firebase'
		/>
		
		<Project
			title='App de cálculos electricos'
			category='Android app'
			tech1='Flutter'
			tech2='Firebase'
			tech3='Android studio'
			description='Aplicación inicialmente desarrollada en kodular y posteriormente migrada a flutter para mejorar su rendimiento.
			Permite al usuario ingresar una serie de parámetros y obtener los cálculos necesarios de un proyecto arquitectónico.
			- Calculo de corriente
			- Calculo de voltajes
			- Calibre del conductor
			- Calculo de protección electrica
			'
		/>
		
		
		<Project
			title='Trading bot'
			category='Bot'
			tech1='Python'
			tech2='___ Api'
			tech3='Pycharm IDE'
			description='Sistema automático de consola desarrollado para el calculo y la gestión de datos obtenidos de una API publica,
			para facilitar la toma de decisiones en el ámbito de las operaciones binarias
			'
		/>
		
		<Project
			title='Instagram bot'
			category='Bot'
			tech1='Python'
			tech2='Selenium'
			tech3='Pycharm IDE'
			description='Programa para IDE diseñado para facilitar la automatizacion de tareas en la aplicacion web de instagram,
			con funciones que facilitan al usuario la publicacion de contenido, envio de mensajes, comentar publicaciones...
			'
		/>
		
		<Project
			title='Whatsapp bot'
			category='Bot'
			tech1='Python'
			tech2='Selenium'
			tech3='Pycharm IDE'
			description='Sistema automático de consola desarrollado para el calculo y la gestión de datos obtenidos de una API publica,
			para facilitar la toma de decisiones en el ámbito de las operaciones binarias
			'
		/>
		
		<Project
			title='Item spawner'
			category='Game dev'
			tech1='Unity 5'
			tech2='C#.net'
			tech3='Rider IDE'
			description='Spawner de items desarrollado para el motor de videojuegos unity, con interfaz de editor para
            mejorar la experiencia de usuario, acceso a base de datos, y gestión de probabilidad'
		/>
	
	
	</div>;
}