import { Skill } from '../Components/Skill';
import './Css/SkilsPage.scss';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	faAndroid, faCss3, faGithub, faHtml5, faPython, faReact, faSass, faUnity
} from '@fortawesome/free-brands-svg-icons';
import React from 'react';


export function SkillsPage() {
	return <div className='skillsPage'>
		
		<Skill>
			<FontAwesomeIcon icon={ faHtml5 } size='4x' />
			<h5>HTML 5</h5>
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faCss3 } size='4x' />
			<h5>CSS 3</h5>
		
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faSass } size='4x' />
			<h5>SASS</h5>
		
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faGithub } size='4x' />
			<h5>GitHub</h5>
		
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faReact } size='4x' />
			<h5>React</h5>
		
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faPython } size='4x' />
			<h5>Python</h5>
		
		</Skill>
		<Skill>
			<FontAwesomeIcon icon={ faAndroid } size='4x' />
			<h5>Android studio</h5>
		
		</Skill>
		
		<Skill>
			<FontAwesomeIcon icon={ faUnity } size='4x' />
			<h5>Unity 5</h5>
		</Skill>
	</div>;
}