import './Css/InfoPage.scss';
import photoPerfil from '../Assets/Img/fotoPerfil.jpg';
import { useEffect, useRef } from 'react';
import { gsap } from 'gsap';


export function InfoPage() {
	let nameAnim = useRef( null );
	let jobAnim = useRef( null );
	let hrAnim = useRef( null );
	let imgAnim = useRef( null );
	let descriptionAnim = useRef( null );
	
	
	useEffect( () => {
		gsap.from( [ nameAnim.current, jobAnim.current ], 0.8, {
			delay: 0.4, y: 128, stagger: {
				amount: 0.15
			}
		} );
		gsap.from( [ hrAnim.current ], 1.4, {
			delay: 1.2, x: 800
		} );
		gsap.from( [ imgAnim.current, descriptionAnim.current ], 1, {
			delay: 1.2, opacity: 0.0
		} );
	} );
	
	return <div className='infoScreen'>
		<div className='info'>
			<div className='nameContainer'>
				<img src={ photoPerfil } ref={ imgAnim } alt='BenjaminPhoto' />
				<div className='infoName'>
					<h1 ref={ nameAnim }>
						Benjamín Garrido González
					</h1>
					<hr ref={ hrAnim } />
					<h2 ref={ jobAnim }>
						Desarrollador jr.
					</h2>
				</div>
			</div>
			
			<br />
			<div ref={ descriptionAnim }>
				<p>
					Soy un programador de 25 años, autodidacta apasionado por la tecnología, la programación, el diseño
					y la
					experiencia de usuario. Me interesa formar parte del sector tecnológico como desarrollador fullstack
					con
					el fin de poner a prueba mis habilidades obtenidas y desarrollar nuevas en base a la experiencia.
				</p>
				<div className='curriculumLink'>
					<a href='https://github.com/DyxBenjamin/portafolio/raw/gh-pages/Curriculum_BenjaminGarrido.pdf'
					   target='_blank' rel='noreferrer'>Descargar curriculum
					</a>
				</div>
			</div>
		</div>
	</div>;
}