import React from 'react';
import './Css/Social.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faInstagram, faLinkedin } from '@fortawesome/free-brands-svg-icons';


export function Social() {
	return (<div className='social'>
		<a className='icon' href='https://github.com/DyxBenjamin' target='_blank' rel='noreferrer'>
			<FontAwesomeIcon icon={ faGithub } size='3x' />
		</a>
		<a className='icon' href='https://www.instagram.com/benjamingarridog/' target='_blank' rel='noreferrer'>
			<FontAwesomeIcon icon={ faInstagram } size='3x' />
		</a>
		<a className='icon' href='https://www.linkedin.com/in/benjamingarridog/' target='_blank' rel='noreferrer'>
			<FontAwesomeIcon icon={ faLinkedin } size='3x' />
		</a>
	</div>);
}

