import { NavLink } from 'react-router-dom';
import './Css/Navbar.scss';


export function Navbar() {
	return <nav>
		<ul className='menu'>
			<li className='containerMenu'>
				<NavLink className='menuItem' exact to='/portafolio' activeClassName='active'>Home</NavLink>
			</li>
			
			<li className='containerMenu'>
				<NavLink className='menuItem' to='/projects' activeClassName='active'>Proyectos</NavLink>
			</li>
			
			<li className='containerMenu'>
				<NavLink className='menuItem' to='/skills' activeClassName='active'>Habilidades</NavLink>
			</li>
			
			<li className='containerMenu'>
				<NavLink className='menuItem' to='/contact' activeClassName='active'>Contacto</NavLink>
			</li>
		</ul>
	</nav>;
	
}