import fondo from '../Assets/Img/Fondo.jpg';


export function Project( {
	title = 'default',
	imagen = fondo,
	category = 'Category',
	tech1 = ' ',
	tech2 = ' ',
	tech3 = ' ',
	description = 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ab accusantium asperiores autem beatae consectetur dolor doloribus dolorum et libero maiores necessitatibus officia pariatur porro saepe, suscipit tenetur veritatis voluptates, voluptatibus'
} ) {
	return <div className='projectContainer'>
		<img src={ imagen } alt='BenjaminPhoto' />
		
		<div className='descriptionContainer'>
			<h2 className='projectTitle'>
				{ title }
			</h2>
			<div className='description'>
				<ul className='techContainer'>
					<li className='category'>{ category }</li>
					<li className='tech'>{ tech1 }</li>
					<li className='tech'>{ tech2 }</li>
					<li className='tech'>{ tech3 }</li>
				</ul>
				
				<p>{ description }</p>
			</div>
		</div>
	
	</div>;
}